<template>
  <el-container class="home-container">
    <!-- 头部区域 -->
    <el-header class="el-header">
      <div class="el-headerImg">
        <!-- <img src="../assets/bluet1.png" alt /> -->
        <span class="icon iconfont outAdmin1">&#xe6e0;</span>
        <span>会畅教育终端版本管理服务</span>
      </div>
      <div>
        <!-- <img src="../assets/bluet1.png" alt class="outAdmin" />
        -->
        <span class="icon iconfont outAdmin">&#xe653;</span>
        <el-dropdown
          class="avatar-container right-menu-item hover-effect"
          trigger="click"
          @command="handleCommand"
        >
          <span class="el-dropdown-link">
            {{userName}}
            <i class="el-icon-caret-bottom" />
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="a">修改密码</el-dropdown-item>
            <el-dropdown-item command="b">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </el-header>
    <el-main class="homeBody">
      <div class="bodyHeader">
        <div class="bodyZhongduan">{{$route.query.type+'升级包（'+tableData.length+'）'}}</div>
        <div>
          <el-button
            type="primary"
            class="bodyButton bodyButton1"
            @click="uploadFileButton"
            v-if="userName == 'admin'"
          >
            <i class="iconfont icon-__update"></i>上传
          </el-button>
          <el-button class="bodyButton" @click="freshData">
            <i class="iconfont icon-__refresh"></i>刷新
          </el-button>
          <el-button class="bodyButton" @click="delMore" v-if="userName == 'admin'">
            <i class="iconfont icon-__delete"></i>批量删除
          </el-button>
          <el-button
            type="primary"
            class="bodyButton1"
            @click="gotOut"
            icon="el-icon-d-arrow-left"
          >返回上级页面</el-button>
        </div>
      </div>
      <div class="tableCrd">
        <el-table
          :data="tableData"
          style="width: 100%"
          class="tab_list"
          @selection-change="handleSelectionChange"
          :header-cell-style="{background:'#fff'}"
          :cell-style="{background:'#f0f8ff'}"
        >
          <el-table-column type="selection" align="center" min-width="30px" />
          <el-table-column label="序号" min-width="40px">
            <template slot-scope="scope">
              <el-button
                type="text"
                size="medium"
                style="font-size:16px;font-family:Microsoft YaHei;font-weight:500;text-decoration:underline;color:#666666;"
              >{{ scope.$index | indexMethod }}</el-button>
            </template>
          </el-table-column>
          <el-table-column prop="Version" label="版本" align="center" min-width="200px"></el-table-column>
          <el-table-column label="版本日志" align="center" min-width="200px">
            <template slot-scope="{ row }">
              <el-popover
                placement="bottom"
                title="详细信息"
                width="200"
                trigger="hover"
                :content="row.Detail"
              >
                <el-button slot="reference" type="text">查看详细</el-button>
              </el-popover>
            </template>
          </el-table-column>

          <el-table-column label="操作" align="center">
            <template v-slot="{row}">
              <!-- <div class="icon_caozuo" style="margin-right:30px">
                <i class="iconfont icon-__download icon_download" style="margin-right:5px"></i>
                <span>下载</span>
              </div>-->
              <a
                :href="'http://'+row.Address"
                target="_blank"
                style="margin-right:30px;text-decoration:none;color:rgb(102,102,102)"
              >
                <i class="iconfont icon-__download icon_download" style="margin-right:5px"></i>
                <span>下载</span>
              </a>
              <div
                class="icon_caozuo"
                @click="delTipsHandle(row)"
                style="cursor:pointer"
                v-if="userName == 'admin'"
              >
                <i class="iconfont icon-__delete"></i>
                <span>删除</span>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-main>
    <!-- 上传文件 -->
    <el-dialog
      title="上传版本文件"
      :visible.sync="uploadFileVisible"
      width="450px"
      style="text-align: left;"
    >
      <div class="upload-file-box">
        <el-upload
          ref="upload"
          action="#"
          :http-request="uploadFile"
          :show-file-list="showFileList"
          :on-remove="handleRemove"
          :limit="1"
          :on-exceed="handleExceed"
          style="width:100%;margin:0 auto"
          :before-upload="beforeAvatarUpload"
        >
          <el-input
            v-model="fileName"
            placeholder="上传文件"
            autocomplete="off"
            clearable
            @clear="handleRemove"
            style="width:100%;"
          />
        </el-upload>
        <div style="text-align:center;margin-top:20px">
          <el-input type="textarea" maxlength="1024" rows="5" v-model="detail"></el-input>
        </div>
        <div style="text-align:center;margin-top:20px">
          <el-button
            type="primary"
            class="upload-btn"
            style="background: rgba(31, 113, 204, 1);"
            @click="upload"
            :disabled="updateBtndisable"
          >上传</el-button>
        </div>
      </div>
      <!-- <div slot="footer" class="dialog-footer" style="text-align: center;"></div> -->
    </el-dialog>
    <!-- 修改密码 -->
    <el-dialog
      title="修改密码"
      :visible.sync="passwordVisible"
      width="450px"
      style="text-align: left;"
      :close-on-click-modal="false"
    >
      <!-- <div class="del-box">
        <div class="del-icon iconfont iconalert-delete"></div>
        <div class="text">确认删除文件吗？</div>
      </div>-->
      <div class="del-box" style="margin: 20px 0 20px 0;">
        <el-form label-width="110px" ref="passwordForm" :rules="rules" :model="passwordForm">
          <el-form-item label="新密码：" prop="newPassword">
            <el-input
              type="password"
              v-model="passwordForm.password"
              style="width:272px;"
              placeholder="请输入密码"
            ></el-input>
          </el-form-item>
          <el-form-item label="确认新密码：" prop="newPasswordCheck">
            <el-input
              type="password"
              v-model="passwordForm.passwordEqual"
              style="width:272px;"
              placeholder="请输入确认密码"
            ></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer" style="text-align: center;">
        <el-button type="primary" class="save-btn text" @click="changePassword">确定</el-button>
      </div>
    </el-dialog>
    <!-- 删除 -->
    <el-dialog title="提示" :visible.sync="tipsVisible" width="450px" style="text-align: left;">
      <div class="del-box">
        <!-- <div class="del-icon iconfont iconalert-delete"></div> -->
        <span
          class="icon iconfont"
          style="font-size:30px;color:#D04B31;margin-right:10px;margin-left:25%;"
        >&#xe650;</span>
        <div class="text">确认删除文件吗？</div>
      </div>
      <div slot="footer" class="dialog-footer" style="text-align: center;">
        <el-button type="primary" class="save-btn text" @click="delAloneData">删除</el-button>
      </div>
    </el-dialog>
    <!-- 批量删除 -->
    <el-dialog title="提示" :visible.sync="delMoreVisible" width="450px" style="text-align: left;">
      <div class="del-box">
        <!-- <div class="del-icon iconfont iconalert-delete"></div> -->
        <span
          class="icon iconfont"
          style="font-size:30px;color:#D04B31;margin-right:10px;margin-left:13%;"
        >&#xe650;</span>
        <div class="text">{{`确定要删除所选的${selections.length}条数据吗？`}}</div>
      </div>
      <div slot="footer" class="dialog-footer" style="text-align: center;">
        <el-button type="primary" class="save-btn text" @click="delMoreData">删除</el-button>
      </div>
    </el-dialog>
  </el-container>
</template>
<script>
export default {
  data() {
    var validatePass1 = (rule, value, callback) => {
      if (this.passwordForm.password == '') {
        callback(new Error('请再次输入密码'))
      } else if (
        this.passwordForm.password.length > 15 ||
        this.passwordForm.password.length < 6
      ) {
        callback(new Error('长度在 6 到 15 个字符'))
      } else {
        callback()
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (this.passwordForm.passwordEqual == '') {
        callback(new Error('请再次输入密码'))
      } else if (
        this.passwordForm.passwordEqual.length > 15 ||
        this.passwordForm.passwordEqual.length < 6
      ) {
        callback(new Error('长度在 6 到 15 个字符'))
      } else if (
        this.passwordForm.passwordEqual !== this.passwordForm.password
      ) {
        callback(new Error('确认密码与新密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      visibleDetail: false,
      detail: '',
      updateBtndisable: false,
      //修改密码
      passwordForm: {
        password: '',
        passwordEqual: ''
      },
      rules: {
        newPassword: [
          {
            validator: validatePass1,
            trigger: 'blur'
          }
        ],
        newPasswordCheck: [
          {
            validator: validatePass2,
            trigger: 'blur'
          }
        ]
      },
      tableData: [],
      addTerminalForm: {
        name: '',
        password: ''
      },
      uploadFileVisible: false,
      passwordVisible: false,
      tipsVisible: false,
      delMoreVisible: false,
      // 文件上传
      showFileList: false,
      uploadfile: {},
      fileName: '',
      fileStatus: 'unready',
      // 单独删除
      selection: [],
      // 批量删除用
      selections: [],
      userName: ''
    }
  },
  created() {
    this.userName = window.localStorage.getItem('user')
    this.getUpdateList()
  },
  filters: {
    // 序号加0判断
    indexMethod(index) {
      if (index < 9) {
        return '0' + (index + 1)
      } else {
        return index + 1
      }
    }
  },
  methods: {
    // 刷新数据
    freshData() {
      this.getUpdateList()
    },
    // 获取升级包列表
    async getUpdateList() {
      const { data: res } = await this.$http.get('package', {
        params: {
          device: this.$route.query.id
        }
      })
      if (res.Code == 200) {
        if (res.Data == null) {
          this.tableData = []
        } else {
          this.tableData = res.Data
        }
      }
    },
    // 修改密码框出现
    handleCommand(command) {
      if (command === 'a') {
        this.passwordVisible = true
      } else {
        this.$router.push({ name: 'Login' })
        window.sessionStorage.removeItem('token')
      }
    },
    // 修改密码
    async changePassword() {
      var that = this
      var paramsid = this.userName == 'admin' ? 0 : 1
      this.$refs.passwordForm.validate(valid => {
        if (!valid) {
          return false
        } else {
          var url = window.location.origin + '/upgrade/user/' + paramsid
          var request = new XMLHttpRequest()
          request.open('put', url, true)
          var sendData = {
            Password: that.passwordForm.passwordEqual,
            Username: this.userName
          }
          var sendDataJSON = JSON.stringify(sendData)
          request.send(sendDataJSON)
          request.onload = function(e) {
            if ((request.readyState == 4) & (request.status == 200)) {
              that.$message.success('修改成功')
              that.passwordVisible = false
              that.$router.push({ name: 'Login' })
              window.sessionStorage.removeItem('token')
            } else {
              that.$message.error('修改失败')
              that.passwordVisible = false
            }
          }
        }
      })
    },
    // 返回上一级
    gotOut() {
      this.$router.push({
        name: 'Home'
      })
    },
    // 上传
    uploadFileButton() {
      this.uploadFileVisible = true
    },
    // 删除
    async delTipsHandle(row) {
      this.tipsVisible = true
      this.selection = [row.Id]
    },
    async delAloneData() {
      const { data: res } = await this.$http.post(
        'package/delete',
        this.selection
      )
      if (res.Code == 200) {
        this.$message.success('删除成功')
        this.tipsVisible = false
        this.getUpdateList()
      } else {
        this.$message.error('删除失败')
      }
    },

    handleSelectionChange(val) {
      this.selections = val
    },
    // 批量删除
    delMore() {
      if (this.selections.length > 0) {
        this.delMoreVisible = true
      } else {
        this.$message.warning('请选择要删除的数据')
      }
    },
    async delMoreData() {
      let lists = []
      this.selections.forEach(item => lists.push(item.Id))
      const { data: res } = await this.$http.post('package/delete', lists)
      if (res.Code == 200) {
        this.$message.success('删除成功')
        this.delMoreVisible = false
        this.getUpdateList()
      } else {
        this.$message.error('删除失败')
      }
    },
    // 文件上传
    handleRemove() {
      this.$refs.upload.clearFiles()
      this.uploadfile = ''
    },
    uploadFile(f) {
      if (f !== undefined) {
        this.uploadfile = f
        this.fileName = f.file.name
      }
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 1 个文件，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      )
    },
    // 文件上传校验
    beforeAvatarUpload(file) {
      let fileArray = file.name.split('.')
      let fileType = fileArray[fileArray.length - 1]
      if (fileType == 'upgrade' || fileType == 'apk') {
        return true
      } else {
        this.$message({
          message: '文件上传格式不对，上传文件名以.upgrade或.apk结尾',
          type: 'warning'
        })
        return false
      }
    },
    // 点击上传
    upload() {
      var that = this
      if (Object.keys(this.uploadfile).length > 0) {
        var url =
          window.location.origin +
          '/upgrade/package?device=' +
          this.$route.query.id +
          '&detail=' +
          this.detail
        var request = new XMLHttpRequest()
        request.upload.onloadstart = function() {
          that.updateBtndisable = true
        }
        request.onload = function(res) {
          if (JSON.parse(res.currentTarget.response).Code == 200) {
            that.$message.success('上传成功')
            that.$refs.upload.clearFiles()
            that.uploadfile = ''
            that.fileName = ''
            that.uploadFileVisible = false
            that.updateBtndisable = false
            that.getUpdateList()
            that.detail = ''
          } else {
            that.$message.error('上传失败')
            that.$refs.upload.clearFiles()
            that.uploadfile = ''
            that.fileName = ''
            that.uploadFileVisible = false
            that.updateBtndisable = false
            that.detail = ''
          }
        }
        var fd = new FormData()
        fd.append('file', this.uploadfile.file)
        request.open('POST', url, true)
        request.send(fd)
      } else {
        this.$message.warning('文件不能为空')
      }
    }
  }
}
</script>
