<template>
  <div class="login_container">
    <div class="login_box">
      <img src="../assets/logo.png" alt class="logot" />
      <span class="zdtitle">会畅教育终端版本管理服务</span>
      <el-form
        ref="loginFormRef"
        :inline="true"
        :model="loginForm"
        :rules="loginFormRules"
        class="demo-form-inline"
      >
        <el-form-item prop="user">
          <el-input
            v-model="loginForm.user"
            placeholder="请输入账号"
            prefix-icon="iconfont icon-__sign-user"
          ></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            v-model="loginForm.password"
            placeholder="请输入密码"
            prefix-icon="iconfont icon-__sigg-code"
            type="password"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="login" style="background:rgba(0,68,194,0.5);">登录</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 这是登录表单的数据绑定对象
      loginForm: {
        user: '',
        password: ''
      },
      // 表单的验证规则对象
      loginFormRules: {
        user: [
          { required: true, message: '请输入登录名称', trigger: 'blur' },
          { min: 3, max: 10, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入登录密码', trigger: 'blur' },
          { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  mounted(){
    window.sessionStorage.removeItem("token")
    window.localStorage.removeItem("user")
  },
  created(){
    document.onkeydown = (e)=>{
      let key = window.event.keyCode
      if(key == 13){
        this.login()
      }
    }
  },
  methods: {
    login() {
      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.get('user/login', {
          params: {
            password: this.loginForm.password,
            username: this.loginForm.user
          }
        })
        if (res.Code == 200) {
          this.$message.success('登录成功')
          window.sessionStorage.setItem('token', 'tokenbypeople')
          window.localStorage.setItem('user',this.loginForm.user)
          this.$router.push('/home')
        }else{
          this.$message.error('账号或密码错误，请重新输入！')
        }
        
      })
    }
  }
}
</script>
<style lang="less" scoped>
.login_container {
  background: url(../assets/denglu.png) no-repeat;
  height: 100%;
  width: 100%;
  height: 100%;
  min-width: 1024px;
  z-index: -10;
  zoom: 1;
  background-size: 100% 100%;
  -webkit-background-size: 100% 100%;
  -o-background-size: 100% 100%;
}
.login_box {
  position: absolute;
  top: 300px;
  left: 306px;
}
.logot {
  width: 228px;
  height: 62px;
  display: inline-block;
  margin-right: 22px;
}
.zdtitle {
  font-size: 30px;
  font-weight: 300;
  color: #fff;
  position: relative;
  top: -16px;
}
.demo-form-inline {
  margin-top: 66px;
}
.el-form--inline .el-form-item {
  margin-right: 16px;
}
.el-input /deep/ .el-input__inner {
  width: 20vw;
  max-width: 400px;
  height: 54px;
  line-height: 54px;
  padding-left: 60px;
  // background: #7ca7ce;
  // background: transparent;
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
  font-size: 20px;
  // outline: none;
  // border: #7EA8CF;
}
// .el-input /deep/ .el-input__inner:focus {
//     border-color: none!important;
//     outline: none!important;
// }
.el-button {
  width: 120px;
  height: 54px;
  font-size: 18px;
  border-radius: 10px;
  background: #3885bf;
  border: 1px solid rgba(166, 212, 255, 1);
  border-radius: 10px;
}
.el-button--primary:focus,
.el-button--primary:hover {
  background: #3885bf;
  border-color: #a6d4ff;
  color: #fff;
  outline: none;
}
.el-form-item /deep/ .iconfont {
  font-size: 20px;
  margin-left: 20px;
  color: #fff;
  font-weight: 400;
}
.el-input /deep/ input::-webkit-input-placeholder {
  color: #fff;
  font-size: 20px;
  font-weight: 400;
}

.el-input /deep/ input:-moz-placeholder {
  color: #fff;
  font-size: 20px;
  font-weight: 400;
}

.el-input /deep/ input::-moz-placeholder {
  color: #fff;
  font-size: 20px;
  font-weight: 400;
}

.el-input /deep/ input:-ms-input-placeholder {
  color: #fff;
  font-size: 20px;
  font-weight: 400;
}
</style>
