<template>
  <el-container class="home-container">
    <!-- 头部区域 -->
    <el-header>
      <div class="el-headerImg">
        <span class="icon iconfont outAdmin1">&#xe6e0;</span>
        <span>会畅教育终端版本管理服务</span>
      </div>
      <div>
        <span class="icon iconfont outAdmin">&#xe653;</span>
        <el-dropdown
          class="avatar-container right-menu-item hover-effect"
          trigger="click"
          @command="handleCommand"
        >
          <span class="el-dropdown-link">
            {{userName}}
            <i class="el-icon-caret-bottom" />
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="a">修改密码</el-dropdown-item>
            <el-dropdown-item command="b">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </el-header>
    <el-main class="homeBody">
      <div class="bodyHeader">
        <div class="bodyZhongduan">{{`终端列表（${tableData.length}）`}}</div>
        <div>
          <el-button
            type="primary"
            class="bodyButton bodyButton1"
            @click="addTerminalHandle"
            v-if="userName == 'admin'"
          >
            <!-- <i class="el-icon-circle-plus-outline"></i> -->
            <span class="icon iconfont">&#xe6d8;</span>
            添加
          </el-button>
          <el-button class="bodyButton" @click="freshData">
            <i class="iconfont icon-__refresh"></i>刷新
          </el-button>
        </div>
      </div>
      <div class="tableCrd">
        <el-table
          :data="tableData"
          style="width: 100%"
          class="tab_list"
          :header-cell-style="{background:'#fff'}"
          :cell-style="{background:'#f0f8ff'}"
        >
          <el-table-column prop="device" label="型号" align="center">
            <template v-slot="{row}">
              <el-button
                type="text"
                size="medium"
                style="font-size:14px;font-family:Microsoft YaHei;font-weight:400;text-decoration:underline;color:rgba(51,51,51,1);"
                @click="modelHandle(row)"
              >{{row.Device}}</el-button>
            </template>
          </el-table-column>
          <el-table-column prop="Latest" label="最新版本" align="center"></el-table-column>
          <el-table-column prop="Comment" label="描述" align="center"></el-table-column>
          <el-table-column label="操作" align="center" v-if="userName == 'admin'">
            <template v-slot="{row}">
              <div
                class="icon_caozuo"
                style="margin-right:30px;cursor:pointer"
                @click="editTerminalHandle(row)"
              >
                <i class="iconfont icon-__edit" style="margin-right:5px"></i>
                <span>编辑</span>
              </div>
              <div class="icon_caozuo" @click="delTipsHandle(row)" style="cursor:pointer">
                <i class="iconfont icon-__delete" style="margin-right:5px"></i>
                <span>删除</span>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-main>
    <!-- 修改密码 -->
    <el-dialog
      title="修改密码"
      :visible.sync="passwordVisible"
      width="450px"
      style="text-align: left;"
      :close-on-click-modal="false"
    >
      <!-- <div class="del-box">
        <div class="del-icon iconfont iconalert-delete"></div>
        <div class="text">确认删除文件吗？</div>
      </div>-->
      <div class="del-box" style="margin: 20px 0 20px 0;">
        <el-form label-width="110px" ref="passwordForm" :rules="rules" :model="passwordForm">
          <el-form-item label="新密码：" prop="newPassword">
            <el-input
              type="password"
              v-model="passwordForm.password"
              style="width:272px;"
              placeholder="请输入密码"
            ></el-input>
          </el-form-item>
          <el-form-item label="确认新密码：" prop="newPasswordCheck">
            <el-input
              type="password"
              v-model="passwordForm.passwordEqual"
              style="width:272px;"
              placeholder="请输入确认密码"
            ></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer" style="text-align: center;">
        <el-button type="primary" class="save-btn text" @click="changePassword">确定</el-button>
      </div>
    </el-dialog>
    <!-- 添加终端 -->
    <el-dialog
      title="添加终端"
      :visible.sync="addTerminalVisible"
      width="450px"
      style="text-align: left;"
      @close="resetForm"
    >
      <div class="del-box" style="margin: 20px 0 20px 0;">
        <el-form :model="addTerminalForm" :rules="rules" ref="addTerminalForm" label-width="110px">
          <el-form-item label="型号：" prop="ipInput">
            <el-input v-model="addTerminalForm.device" style="width:272px;" placeholder="请输入型号名称"></el-input>
          </el-form-item>
          <el-form-item label="描述：">
            <el-input v-model="addTerminalForm.comment" style="width:272px;" placeholder="请输入型号描述"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer" style="text-align: center;">
        <el-button type="primary" class="save-btn text" @click="addZhongDuan">确定</el-button>
      </div>
    </el-dialog>
    <!-- 删除 -->
    <el-dialog title="提示" :visible.sync="tipsVisible" width="450px" style="text-align: left;">
      <div class="del-box" style="margin:auto;">
        <!-- <i class="el-icon-circle-close"></i> -->
        <span
          class="icon iconfont"
          style="font-size:30px;color:#D04B31;margin-right:10px;margin-left:25%;"
        >&#xe650;</span>
        <div class="text">确认删除{{datesCopy}}？</div>
      </div>
      <div slot="footer" class="dialog-footer" style="text-align: center;">
        <el-button type="primary" class="save-btn text" @click="deleteZhongDuan">删除</el-button>
      </div>
    </el-dialog>
    <!-- 编辑终端 -->
    <el-dialog
      title="编辑终端"
      :visible.sync="editTerminalVisible"
      width="450px"
      style="text-align: left;"
      @close="resetFormEdit"
    >
      <div class="del-box" style="margin: 20px 0 20px 0;">
        <el-form
          label-width="110px"
          ref="editTerminalForm"
          :model="editTerminalForm"
          :rules="rules"
        >
          <el-form-item label="型号：" prop="ipInputtwo">
            <el-input type="text" v-model.trim="editTerminalForm.device" style="width:272px;"></el-input>
          </el-form-item>
          <el-form-item label="描述：">
            <el-input type="text" v-model.trim="editTerminalForm.comment" style="width:272px;"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer" style="text-align: center;">
        <el-button type="primary" class="save-btn text" @click="editZhongDuan">保存</el-button>
      </div>
    </el-dialog>
  </el-container>
</template>
<script>
export default {
  data() {
    var ipCheck = (rule, value, callback) => {
      if (!this.addTerminalForm.device) {
        callback(new Error('型号不能为空'))
      } else {
        callback()
      }
    }
    var ipChecktwo = (rule, value, callback) => {
      if (!this.editTerminalForm.device) {
        callback(new Error('型号不能为空'))
      } else {
        callback()
      }
    }
    var validatePass1 = (rule, value, callback) => {
      if (this.passwordForm.password == '') {
        callback(new Error('请再次输入密码'))
      } else if (
        this.passwordForm.password.length > 15 ||
        this.passwordForm.password.length < 6
      ) {
        callback(new Error('长度在 6 到 15 个字符'))
      } else {
        callback()
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (this.passwordForm.passwordEqual == '') {
        callback(new Error('请再次输入密码'))
      } else if (
        this.passwordForm.passwordEqual.length > 15 ||
        this.passwordForm.passwordEqual.length < 6
      ) {
        callback(new Error('长度在 6 到 15 个字符'))
      } else if (
        this.passwordForm.passwordEqual !== this.passwordForm.password
      ) {
        callback(new Error('确认密码与新密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      rules: {
        ipInput: [{ validator: ipCheck, trigger: 'blur' }],
        ipInputtwo: [{ validator: ipChecktwo, trigger: 'blur' }],
        newPassword: [
          {
            validator: validatePass1,
            trigger: 'blur'
          }
        ],
        newPasswordCheck: [
          {
            validator: validatePass2,
            trigger: 'blur'
          }
        ]
      },
      userName: '',
      tableData: [],
      passwordVisible: false,
      addTerminalVisible: false,
      tipsVisible: false,
      editTerminalVisible: false,
      // 添加终端
      addTerminalForm: {
        device: '',
        comment: ''
      },
      // 编辑终端
      editTerminalForm: {
        device: '',
        comment: ''
      },
      //修改密码
      passwordForm: {
        password: '',
        passwordEqual: ''
      },
      dates: '',
      datesCopy: '',
      userId: ''
    }
  },
  created() {
    this.userName = window.localStorage.getItem('user')
    this.getDataDevice()
    this.getUserId()
  },
  methods: {
    freshData() {
      this.getDataDevice()
    },
    async getUserId() {
      const { data: res } = await this.$http.get('user', {})
    },
    // 请求数据
    async getDataDevice() {
      const { data: res } = await this.$http.get('device/list', {
        // params: { token: window.sessionStorage.getItem('token') }
      })
      if (res.Code === 200) {
        if (res.Data == null) {
          this.tableData = []
        } else {
          this.tableData = res.Data
        }
      }
    },
    resetForm() {
      this.$refs.addTerminalForm.resetFields()
      this.addTerminalForm = {
        device: '',
        comment: ''
      }
    },
    resetFormEdit() {
      this.$refs.editTerminalForm.resetFields()
      this.editTerminalForm = {
        device: '',
        comment: ''
      }
    },
    // 修改密码框出现
    handleCommand(command) {
      if (command === 'a') {
        this.passwordVisible = true
      } else {
        this.$router.push({ name: 'Login' })
        window.sessionStorage.removeItem('token')
      }
    },
    // 修改密码
    async changePassword() {
      var that = this
      var paramsid = this.userName == 'admin' ? 0 : 1
      this.$refs.passwordForm.validate(valid => {
        if (!valid) {
          return false
        } else {
          var url = window.location.origin + '/upgrade/user/' + paramsid
          var request = new XMLHttpRequest()
          request.open('put', url, true)
          var sendData = {
            Password: that.passwordForm.passwordEqual,
            Username: this.userName
          }
          var sendDataJSON = JSON.stringify(sendData)
          request.send(sendDataJSON)
          request.onload = function(e) {
            if ((request.readyState == 4) & (request.status == 200)) {
              that.$message.success('修改成功')
              that.passwordVisible = false
              that.$router.push({ name: 'Login' })
              window.sessionStorage.removeItem('token')
            } else {
              that.$message.error('修改失败')
              that.passwordVisible = false
            }
          }
        }
      })
    },
    // 型号
    modelHandle(row) {
      this.$router.push({
        name: 'Model',
        query: { type: row.Device, id: row.Id }
      })
    },
    // 添加终端
    addTerminalHandle() {
      this.addTerminalVisible = true
    },
    // 删除
    delTipsHandle(row) {
      this.dates = row.Id
      this.datesCopy = row.Device
      this.tipsVisible = true
    },
    // 删除
    async deleteZhongDuan() {
      var that = this
      var xhr = new XMLHttpRequest()
      var url = window.location.origin + '/upgrade/device/' + this.dates
      xhr.open('delete', url, true)
      xhr.send(null)
      xhr.onload = function(e) {
        if ((xhr.status == 200) & (xhr.readyState == 4)) {
          that.tipsVisible = false
          that.getDataDevice()
        }
      }
      // const { data: res } = await this.$http.delete(
      //   'device',
      //   {
      //     params: this.dates

      //   }
      // )
      // if (res.code === 200) {
      //   this.$message.success(res.msg)
      // } else {
      //   this.$message.error(res.msg)
      // }
    },
    // 编辑
    editTerminalHandle(row) {
      this.editTerminalForm.device = row.Device
      this.editTerminalForm.comment = row.Comment
      this.dates = row.Id
      this.editTerminalVisible = true
    },
    // 添加终端
    addZhongDuan() {
      this.$refs.addTerminalForm.validate(valid => {
        if (!valid) {
          return false
        }
        this.addTerminalFunction()
      })
    },
    async addTerminalFunction() {
      const { data: res } = await this.$http.post('device', {
        device: this.addTerminalForm.device,
        comment: this.addTerminalForm.comment
      })
      if (res.Code === 200) {
        this.$message.success(res.Msg)
        this.addTerminalVisible = false
        this.getDataDevice()
      } else {
        this.$message.error(res.Msg)
      }
    },
    // 编辑终端
    async editZhongDuan() {
      var that = this
      this.$refs.editTerminalForm.validate(valid => {
        if (!valid) {
          return false
        }
        var xhr = new XMLHttpRequest()
        var url = window.location.origin + '/upgrade/device/' + this.dates
        xhr.open('put', url, true)
        var data = JSON.stringify(this.editTerminalForm)
        xhr.send(data)
        xhr.onload = function(e) {
          if ((xhr.status == 200) & (xhr.readyState == 4)) {
            that.editTerminalVisible = false
            that.getDataDevice()
          }
        }
      })

      // const { data: res } = await this.$http.put(
      //   'device',
      //   {},
      //   {
      //     params: {
      //       device: this.editTerminalForm.device,
      //       comment: this.editTerminalForm.comment
      //     }
      //   }
      // )
      // if (res.code === 200) {
      //   this.$message.success(res.msg)
      // } else {
      //   this.$message.error(res.msg)
      // }
    }
  }
}
</script>
